:root {
  --transform: cubic-bezier(.4, 0, .2, 1);
  --time-transform: .25s;
  --body-bg-light: #f6f6f6;
  --body-bg-dark: #202024;
  --header-bg-dark: #111;
  --header-bg-light: #fff;
  --violet: #4f2ee8;
  --yellow: #eac645;
  --white: #fff;
  --top-category-title: #b4afaf;
  --text-color: #111;
  --text-color-dark: #fff;
  --bgc-modal: #1116;
  --modal-text-light: #1119;
  --modal-text-dark: #ffffff80;
  --modal-text-autor: #b4afaf;
  --scroll-color: #e0e0e0;
  --scroll-color-dark: #1119;
  --text-color-span-dark: #fff9;
  --text-shop-list: #fff9;
  --text-shop-list-light: #0009;
  --delete-button: #4f2ee8;
}

