}

.switch {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  transition: all .4s;
  position: absolute;
  inset: 0;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.slider:before {
  content: "";
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  width: 20px;
  height: 20px;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 18px;
  box-shadow: 1px 1px 2px #0000001a, 0 2px 2px #0000001a, 1px 2px 4px #0000001a;
}

input:checked + .slider {
  background: linear-gradient(180deg, var(--violet) 0%, #dcdcdc 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

input:checked + .slider:before {
  transform: translateX(-16px);
}

.slider, .round {
  border-radius: 24px;
}

.round:before {
  border-radius: 50%;
}

input:checked + .round:before {
  background-color: #fff;
}

