.modal-wrapper {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: var(--bgc-modal);
  z-index: 500;
  transition: all var(--time-transform) var(--transform);
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.modal-window-wrap {
