.header {
  width: 100%;
  padding-top: 13px;
  padding-bottom: 13px;
  border: 1.5px solid var(--text-color);
  border-radius: 0 0 8px 8px;
  background-color: var(--header-bg-light);
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  /* opacity: 0; */
  /* visibility: hidden; */
  /* pointer-events: none; */
  display:none;
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.header-buttons {
  display: none;
}

.header-logo-text {
  color: var(--text-color);
  font-weight: 700;
  font-size: 24px;
  line-height: 1.29;
  letter-spacing: -0.04em;
}

.header-logo {
  margin-right: 40px;
  display: flex;
  gap: 8px;
  align-items: center;
}

.nav-list {
  display: none;
}

.signup-header {
  display: none;
}

.mobile-wrapper,
.mobile-wrapper-signup {
  position: fixed;
  width: 100vw;
  height: 100%;
  top: 0;
  left: 0;
  background-color: var(--violet);
  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1;
  background: radial-gradient(
    189.32% 190.93% at 59.76% -23.42%,
    #4f2ee8 18.03%,
    #fff 100%
  );
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25),
    0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.mobile-container {
  width: 100%;
  max-width: 375px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  position: relative;
}

.signup-burger {
  display: inline-flex;
  justify-content: space-between;
  color: #fff;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: -0.56px;
  width: 184px;
  height: 48px;
  background-color: var(--violet);
  border-radius: 18px;
  border: 1.5px solid #111;
  padding: 14px;
  z-index: 1;
  position: fixed;
  top: 104px;
}

.mbtn {
  display: inline-flex;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
}

.mbtn .icon-cross {
  display: none;
}

.mbtn.is-open .icon-cross {
  display: block;
}

.mbtn.is-open .icon-menu {
  display: none;
}

.icon-cross {
  stroke: #111;
}

.icon-menu {
  stroke: #111;
}

.div-stephen {
  position: absolute;
  top: 84px;
  display: flex;
  align-items: center;
}

.stephen {
  margin-right: 9px;
}

.text-stephen {
  margin: 0;
  color: #fff;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: -0.44px;
}

.nav-item {
  color: #f3f3f3;
  font-weight: 700;
  line-height: 1.29;
  letter-spacing: -0.56px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  gap: 4px;
}

.nav-item-current {
  display: flex;
  padding: 8px 16px;
  border-radius: 36px;
  background: var(--yellow);
  color: var(--text-color);
}

.list-burger {
  margin: 0;
  padding-left: 0;
  position: absolute;
  top: 257px;
  left: 129px;
  display: flex;
  flex-direction: column;
  align-content: stretch;
  align-items: flex-start;
  gap: 16px;
  z-index: 2;
}

.icon-sl {
  vertical-align: middle;
}

.log-out {
  z-index: 1;
  position: fixed;
  bottom: 20px;
  vertical-align: middle;
  padding: 0;
  width: 118px;
  height: 48px;
  flex-shrink: 0;
  border-radius: 18px;
  border: 1.5px solid var(--text-color);
  background: var(--white);
  color: var(--text-color);
  font-weight: 500;
  line-height: 18px;
  letter-spacing: -0.56px;
}

.logout-svg {
  vertical-align: middle;
}

.image-menu {
  position: fixed;
  bottom: 0;
  width: 100%;
}

@media screen and (min-width: 767px) {
  .nav {
    display: none;
    margin-right: auto;
    align-items: center;
  }
  .nav-list {
    display: flex;
    align-items: center;
    gap: 24px;
  }

  .nav-item {
    color: var(--text-color);
  }

  .icon-sl-header {
    fill: #111;
  }
  .nav-item-current {
    display: flex;
    padding: 8px 16px;
    border-radius: 36px;
    background: var(--yellow);
  }
  .sign-up-svg {
    fill: #fff;
  }
  .header-buttons {
    display: block;
  }
  .toggle-switch {
    margin: 0px 24px 0px auto;
  }
  .sign-up-btn {
    display: flex;
    align-items: center;
    width: 184px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 18px;
    border: 1.5px solid #111;
    background: var(--violet);
    margin-right: 222px;
    margin-left: 20px;
  }
  .signup-header {
    display: inline-flex;
    justify-content: space-between;
    color: var(--white);
    font-weight: 500;
    line-height: 18px;
    letter-spacing: -0.56px;
    width: 184px;
    height: 48px;
    background-color: var(--violet);
    border-radius: 18px;
    border: 1.5px solid #111;
    padding: 14px;
  }

  .div-stephen-header {
    display: none;
    width: 184px;
    height: 45px;
    align-items: center;
    padding: 8px 12px;
    gap: 9px;
    border-radius: 36px;
    background: var(--violet);
  }
  .wrapper-button {
    position: relative;
  }

  .log-out {
    z-index: 0;
    position: absolute;
    bottom: -48px;
    vertical-align: middle;
    margin-right: 237px;
    margin-bottom: 0;
    margin-left: 0;
    padding: 14px;
    width: 184px;
    height: 48px;
    display: none;
    justify-content: space-between;
  }

  .log-out.show {
    display: flex;
  }

  .mobile-menu {
    display: none;
  }
}

@media screen and (min-width: 1440px) {
  .header {
    padding-top: 22px;
    padding-bottom: 22px;
  }
}

.toggle-switch {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0px 14px 0px auto;
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
}

.switch input {
  opacity: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.1);
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: '';
  height: 20px;
  width: 20px;
  left: 18px;
  bottom: 2px;

  background: linear-gradient(180deg, #ffffff 0%, #f7f7f7 100%);
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), 0px 2px 2px rgba(0, 0, 0, 0.1),
    1px 2px 4px rgba(0, 0, 0, 0.1);

  transition: 0.4s;
}

input:checked + .slider {
  background: linear-gradient(180deg, var(--violet) 0%, #dcdcdc 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.1);
}

input:checked + .slider:before {
  transform: translateX(-16px);
}

.slider,
.round {
  border-radius: 24px;
}

.round::before {
  border-radius: 50%;
}

input:checked + .round::before {
  background-color: #fff;
}
